// Global body background color
// Update reseted styles by backbase-portal.css
.html-content-formatter {

  ul {
    list-style-type:  disc;
  }

  ol {
    @extend .mx-0;
    list-style-type: decimal;
  }

  ol, ul {
    @extend .pl-4;
  }

  li {
    @extend .pl-0;
    list-style: inherit;
  }
}


