@font-face {
    font-family: 'Roboto';
    src: url('#{$fonts-path}/Roboto/Roboto-Regular.eot');
    src: url('#{$fonts-path}/Roboto/Roboto-Regular.eot#iefix') format('embedded-opentype'),
        url('#{$fonts-path}/Roboto/Roboto-Regular.woff2') format('woff2'),
        url('#{$fonts-path}/Roboto/Roboto-Regular.woff') format('woff'),
        url('#{$fonts-path}/Roboto/Roboto-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

