.page-layout-tabs {
  & > .nav-tabs {
    @extend .row;
    @extend .px-3;

    background-color: $page-header-background-color;
    margin-top: -$base-padding-height;
    .nav-item {
      cursor: pointer;
      &.active {
        color: $primary;
      }
    }
  }

  .tab-content {
    @extend .py-4;
  }
}



