@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url('#{$material-icons-font-path}/MaterialIcons-Regular.eot'); /* For IE6-8 */
  src: local('#{$material-icons-font-path}/Material Icons'),
       local('#{$material-icons-font-path}/MaterialIcons-Regular'),
       url('#{$material-icons-font-path}/MaterialIcons-Regular.woff2') format('woff2'),
       url('#{$material-icons-font-path}/MaterialIcons-Regular.woff') format('woff'),
       url('#{$material-icons-font-path}/MaterialIcons-Regular.ttf') format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}



