
.dropdown-menu.modal.in,
.dropdown-menu.show,
.dropdown {
  .bicon-toggle-down:before {
    font-size: $selector-chevron-size;
  }

  &.open {
    .dropdown-menu {
      @include animation(transform, $animation-dropdown-menu-duration);

      transform: scale(1);

      .dropdown-item.active {
        border-left: $selected-left-border-highlight
      }
    }

    .bicon-toggle-down {
      transform: rotate(180deg);
      transition:all $animation-dropdown-menu-duration $mdc-animation-standard-curve-timing-function;
    }
  }

  .btn-dropdown-toggle {
    font-size: 1rem;
    background-color: $light-blue;
  }

  &.selected .btn-dropdown-toggle {
    border-left: $selected-left-border-highlight;
  }
}

.rb-navbar-top, .rb-sidebar {
  .dropdown-menu {
    @include animation(transform, $animation-dropdown-menu-duration);

    transform: scale(0);
    display: inline-block;
    width: 100%;
    font-size: $base-font-size;
    border: none;
    box-shadow: $box-shadow-2;

    .dropdown-header {
      font-size: $base-font-size;
      padding: $tiny-padding-height $small-padding-height;
    }

    .dropdown-option {
      a {
        width: 100%;
        display: inline-block;
        padding: $tiny-padding-height $small-padding-height;
        color: $black;

        &:hover {
          text-decoration: none;
          background-color: $accent-blue-a12;
        }
      }
    }
  }
}



