
// Add border width to outline buttons, different from solid buttons
@each $color, $value in $theme-colors {
  $hover-color: map-get($theme-outline-hover-colors, $color);
  .btn-outline-#{$color} {
    border-width: $btn-outline-border-width;
    &:hover {
      color: color-yiq($hover-color);
      background-color: $hover-color;
    }
  }
}

.btn-icon {
  @include gradient-bg(transparent);

  height: 40px;
  width: 40px;
  line-height: 40px;
  padding: 4px;
  border: none;
  position: relative;
  color: transparent;
  border-color: transparent;

  @include hover {
    @include gradient-bg(transparent);

    color: transparent;
    border-color: transparent;
  }

  &-glow {
    z-index: 2;

    &:after {
      @include animation(transform);

      z-index: 1;
      content: "";
      position: absolute;
      background-color: $accent-blue-a12;
      height: 100%;
      width: 100%;
      display: inline-block;
      top: 0;
      left: 0;
      border-radius: 100%;
      transform: scale(0);
      transform-origin: 50% 50%;
    }

    &:hover:after {
      @include animation(transform);

      transform-origin: 50% 50%;
      background-color: $black-a4;
      transform: scale(1);
    }
  }

  &-large > i {
    font-size: 2rem;
  }

  > i {
    height: 2rem;
    line-height: 2rem;
    width: 2rem;
    color: $black-a40;
  }

  &:active:focus, &:focus {
    outline: none;
    box-shadow: none!important;
  }
}

.btn-link {
  color: $btn-link-color;

  @include hover {
    color: $btn-link-color;
    background-color: $btn-link-bg-color;
  }
}



