.rb-navbar-top {
  .popover {
    @include animation(transform, $animation-dropdown-menu-duration);

    overflow: hidden;
    display: inline-block;
    opacity: 1;
    transform: scale(0);

    &.bottom {
      margin: 0;
    }

    &.in {
      @include animation(transform, $animation-dropdown-menu-duration);

      opacity: 1;
      display: inline-block;
      transform: scale(1);
    }

    .arrow {
      display: none;
    }
  }
}



