.rb-navbar-top {
  .navbar-toggler {
    @extend .btn-icon;
    @extend .btn-icon-large;
    @extend .btn-icon-glow;

    border: none;
    background-color: $white;

    .navbar-toggler-icon {
      @extend .d-flex;
      @extend .bicon;

      background-image: none;
      color: $black-a60;
      font-size: 2rem !important;

      &:before {
        @extend .bicon-menu;
      }
    }
  }
}

.navbar-expand-md {
  @include media-breakpoint-up(md) {
    .navbar-toggler {
      display: block;
    }
  }
}



