.ext-bb-create-new-ng {
  .rb-sidebar & {
    display: none;
    text-align: center;
    padding: $base-padding-height 0;

    @include media-breakpoint-down(sm) {
      .placeholder {
        display: inline-block;
      }
    }
  }

  .btn-group {
    &.dropdown {
      .dropdown-toggle {
        border-left: $dropdown-toggle-border;
      }
    }

    > .btn {
      padding: 0 $tiny-padding-height;
      align-items: center;

      & > i {
        height: 2rem;
        font-size: 1.375rem;
        line-height: 2rem;
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .sidebar-is-open .rb-sidebar .ext-bb-create-new-ng {
    display: block;
  }
}


.ext-bb-create-new-ng {
  @include media-breakpoint-down(md) {
    .placeholder {
      display: none;
    }

    .dropdown-menu {
      min-width: 120px;
    }
  }

  @include media-breakpoint-down(sm) {
    display: none;
  }
}



